import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import { NgbModule } from '@ng-bootstrap/ng-bootstrap'
import { HotkeyProvider, ConfigProvider, ToolbarButtonProvider } from 'tabby-core'
import { LoveShellHotkeyProvider } from './hotKeyProvider'
import { LoveShellConfigProvider } from './configProvider'
import { LoveShellSettingsComponent } from './loveShellSettings.component'
import { SettingsTabProvider } from 'tabby-settings'
import { LoveShellSettingsTabProvider } from './settingsProvider'
import { LoveShellModalComponent } from 'components/loveShellModal.component'
import { ButtonProvider } from 'buttonProvider'

@NgModule({
    imports: [NgbModule, CommonModule, FormsModule],
    providers: [
        { provide: HotkeyProvider, useClass: LoveShellHotkeyProvider, multi: true },
        { provide: ToolbarButtonProvider, useClass: ButtonProvider, multi: true },
        { provide: ConfigProvider, useClass: LoveShellConfigProvider, multi: true },
        { provide: SettingsTabProvider, useClass: LoveShellSettingsTabProvider, multi: true },
    ],
    entryComponents: [
        LoveShellSettingsComponent,
        LoveShellModalComponent
    ],
    declarations: [
        LoveShellSettingsComponent,
        LoveShellModalComponent
    ],
})

// eslint-disable-next-line @typescript-eslint/no-extraneous-class
export default class LoveShellModule { }
