import { Component } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { LoveShellService } from 'loveShell.service'
import { AppService, BaseTabComponent, SplitTabComponent} from 'tabby-core'
import { BaseTerminalTabComponent } from 'tabby-terminal';

@Component({
    template: require('./loveShellModal.component.pug'),
    styles: [require('./loveShellModal.component.scss')],
})
export class LoveShellModalComponent {
    searchInput: string
    appendCR: boolean
    loading: boolean
    suggestion: string
    error: string

    constructor (
        public modalInstance: NgbActiveModal,
        private loveShell: LoveShellService,
        private app: AppService
    ) { }

    ngOnInit () {
        this.suggestion = ''
        this.appendCR = false
        this.loading = false
    }

    refresh() {
        this.error = '';
    }

    async findSuggestion() {
        this.loading = true
        this.suggestion = ''
        try { 
        this.suggestion = await (await this.loveShell.findSuggestion(this.searchInput))
        this.suggestion = this.suggestion.trim().replace(/\n|\r/g, "");
        }catch {
            this.error = 'no suggestion found'
        }
        this.loading = false
    }

    close () {
        this.modalInstance.close()
        this.app.activeTab.emitFocused()
    }

    apply () {
        this._send(this.app.activeTab, this.suggestion + (this.appendCR ? "\n" : ""))
        this.close()
    }

    _send (tab: BaseTabComponent, cmd: string) {
        if (tab instanceof SplitTabComponent) {
            this._send((tab as SplitTabComponent).getFocusedTab(), cmd)
        }
        if (tab instanceof BaseTerminalTabComponent) {
            let currentTab = tab as BaseTerminalTabComponent
            console.log("Sending " + cmd);
            currentTab.sendInput(cmd)
        }
    }
}