import { Injectable } from '@angular/core'
import { LogService,ConfigService } from 'tabby-core'
import { Configuration, OpenAIApi } from 'openai';

@Injectable({ providedIn: 'root'})
export class LoveShellService {
    private openai:OpenAIApi
    private model: string
    constructor(log: LogService, config: ConfigService){
        log.create('love-shell')
        const configuration = new Configuration({
            apiKey: config.store.loveShell.apiKey || '',
          });
          this.openai = new OpenAIApi(configuration);
          this.model = config.store.loveShell.model || ''
    }

    async findSuggestion (input: string) {
        const response = await this.openai.createCompletionFromModel({
            model: this.model,
            prompt: input+" __NL__ ",
            stop:[' __SH__ ']
          })
        return response.data && response.data.choices[0]? response.data.choices[0].text : 'No suggestion found';
    }

}